<template>
	<div id="openWechatPay">
		<!---------------isPremission==false------------------->
		<div class="noPremission" v-if="isPremission==true">
			<div class="img left">
				<img src="../image/emptypic_pic_normal.png" />
			</div>

			<div class="text left">
				<p class="bigTop"></p>
				<p class="blue bigTop">0755-xxxxyyyy</p>
				<p></p>
				<p>1.</p>
				<p>2.</p>
				<p>3.1. 2. </p>
				<p>3. 2Mbmppngjpegjpggif</p>
				<p>4.</p>
			</div>
		</div>

		<!---->
		<div v-else>

			<!-- ----------isConfig==false------------------>
			<div v-if="isConfig==false" class="notConfig">
				
				<el-form label-width="152px" :model="noConfigForm" label-position="right" ref="noConfigForm">
					<el-form-item label="">
						<p class="small">  xinchihuo.xx.xx.com</p>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="noConfigForm.shopNo"></el-input>
						<p class="small"></p>
					</el-form-item>
					<el-form-item label="API">
						<el-input type="password" v-model="noConfigForm.passWord"></el-input>
						<p class="small"></p>
					</el-form-item>
					<el-form-item label="">
						<el-radio-group v-model="noConfigForm.contributing">
							<el-radio label="" class="yes"></el-radio>
							<el-radio label=""></el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="">
						<div class="img left" v-if="isImg">
							<div class="mask"></div>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+imgSrc" alt="" />
							<i class="el-icon-view" @click='clickImg'></i>
						</div>
						<code-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></code-img>
							<img-upload @send-image='getPhoto'></img-upload>
						<div class="text-warp">
							<p class="small"></p>
						</div>
					</el-form-item>
					<el-form-item>
						<el-button class="submitBtn" @click="submitForm('noConfigForm')"></el-button>
						<el-button class="cancelBtn" @click="resetForm('noConfigForm')"></el-button>
					</el-form-item>
				</el-form>
			</div>

			<!---->
			<div v-else>
				<el-form label-width="152px" :model="noConfigForm" label-position="right" ref="noConfigForm">
					<el-form-item label="">
						<span>{{noConfigForm.shopNo}}</span>
					</el-form-item>
					<el-form-item label="API">
						<span>{{noConfigForm.passWord}}</span>
					</el-form-item>
					<el-form-item label="">
						<span></span>
					</el-form-item>
					<el-form-item label="">
						<el-button class="downLoad"></el-button>
					</el-form-item>
				</el-form>
			</div>

		</div>

	</div>
</template>

<script>
	import ImgUpload from '@/components/upload/SUpload';
	import codeImg from "../../components/showCodeImg";
	export default {
		data() {
			return {
				imgbase_url: imgbase_url, //
				imgSrc: '',
				isImg: false,
				showImg: false,
				isPremission: false,
				isConfig: false,
				noConfigForm: {
					shopNo: "12345678",//
					passWord: "1233456",//API
					contributing: ""//
				}
			}
		},
		components: {
			ImgUpload,
			codeImg,
		},
		methods: {
			getPhoto(res) {
				this.imgSrc = res;
				this.isImg = true;
			},
			clickImg() {
				this.showImg = true;
				// 
				this.imgSrc = this.imgSrc
			},
			viewImg() {
				this.showImg = false;
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						alert('submit!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#openWechatPay {
		margin-left: -45px;
	}
	
	.img {
		width: 50px;
		height: 50px;
		margin-right: 30px;
	}
	
	img {
		width: 100%;
		height: 100%;
	}
	
	.text {
		width: 80%;
		margin-left: 35px;
	}
	
	p {
		font-size: 16px;
		height: 30px;
		line-height: 30px;
	}
	
	.small {
		font-size: 12px;
		color: darkgray;
	}
	
	.blue {
		font-size: 20px;
		color: #00CCFF;
	}
	
	.bigTop {
		height: 50px;
		line-height: 50px;
	}
	
	.notConfig {
		width: 60%;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#openWechatPay {
		.el-radio.yes {
			margin-right: 100px;
		}
		.el-button.submitBtn {
			margin: 0 30px 0 200px;
		}
		.el-button.cancelBtn {
			background: #fff;
			color: #4877E7;
		}
		.el-button.downLoad {
			background: #fff;
			color: #666666;
			border: 1px solid #666666;
		}
		.el-form-item__label {
			text-align: right;
			/*padding-left: 0;*/
			padding-right: 47px;
		}
		.el-input{
			width: 300px;
		}
	}
</style>


